<template>
    <div>
        <Header :FlagAddDetail=true></Header>
        <div class="address">
            <van-cell-group>
                <van-field class="input" v-model="input" placeholder="请输入小区/写字楼/学校等" />
            </van-cell-group>
            <van-button type="info" @click="tijiao()">确认</van-button>
        </div>
        <div class="gao">
            为了满足商家的送餐需求,建议你从列表中选择地址
        </div>
        <div class="nav">
            <ul>
                <li v-for="(v,i) in arr" :key="i" @click="routerAdd(v)" >
                    <p>{{v.name}}</p>
                    <p>{{v.address}}</p> 
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Header from './../../../../Unify/Header'
    export default {
        data() {
            return {
                input:"",
                arr:[]
            }
        },
        components:{
            Header
        },
        mounted() {
            
        },
        methods: {
            tijiao(){
                if(this.input!=""){
                    this.$http.get("http://elm.cangdu.org/v1/pois?type=nearby&keyword="+this.input+"").then((res) => {
                    console.log(res.data);  
                    this.arr=res.data
                    })  
                }else{
                    this.$toast({
                    message: "请输入搜索内容",
                    position: "bottom"
                    })
                  
                }
            },
            routerAdd(v){
                this.$router.push({ 
                    path: "/add",
                    query:{
                        addname:v.name,
                        geohash:v.geohash
                    }
                    })
            }
        },
    }
</script>

<style lang="scss" scoped>
.address{
    width: 100%;
    height: 0.8rem;
    margin-top: 0.6rem;
    background: #fff;
    display: flex;
    
    .van-cell-group{
        width:70% ;
        // height: 0.4rem;
        margin-top: 0.15rem;
        margin-left: 0.2rem;
        .van-field{
            background: #f3f3f3;
            border-radius: 5px;
            border: solid 1px #999;
            font-size: 0.18rem;
        }
    }
    button{
        border-radius: 5px;
        margin-top: 0.15rem;
        margin-left: 0.02rem;
        font-size: 0.18rem;
        padding: 0 0.25rem;
       
    }
}
.gao{
    width: 100%;
    background: rgb(255, 212, 196);
    text-align: center;
    color: orange;
    font-size: 0.18rem;
    padding: 0.05rem;
}
.nav{
    width: 100%;
    ul{
        width: 100%;
        list-style: none;
        
        li{
            width: 100%;
            font-size: 0.24rem;
            color: #999;
            border-bottom:solid 1px #999;
            padding: 0.1rem 0;
        }
    }
}
</style>